@use '../../styles/index.scss' as *;

.inputField {
  display: inline-flex;
  border-radius: 8px;
  padding: 8px 16px;
  transition: all .3s;

  &.secondary {
    border: em(2) solid hsla(0, 0%, 0%, 0.16);
  }

  &:focus-within,
  &.primary {
    border: em(2) solid hsla(219, 100%, 50%, 1);
  }
}

.input {
  @extend %body-l;

  width: 100%;
  padding: 0;
  border: none;
  outline: none;
  font-family: GT Eesti Pro Display;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;

  &::placeholder {
    color: hsla(0, 0%, 0%, 0.42);
  }
}